* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

#root {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.App {
    width: 800px;
}

.post {
    display: flex;
    padding: 15px;
    border: 2px solid teal;
    margin-top: 15px;
    justify-content: space-between;
    align-items: center;
}

.post__btns{
    display: flex;
}

.post-enter {
    -webkit-transform: translateX(-350px);
            transform: translateX(-350px);
}
.post-enter-active {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
    transition: all 500ms ease-in;
}
.post-exit {
    opacity: 1;
}
.post-exit-active {
    -webkit-transform: translateX(-350px);
            transform: translateX(-350px);
    transition: all 500ms ease-in;
}

.page__wrapper{
    margin-top: 30px;
    display: flex;
}

.page {
    padding: 10px;
    border: 1px solid teal;
    cursor: pointer;
}

.page__current {
    border: 2px solid orange;
    font-weight: bold;
}

.navbar{
    height: 50px;
    width: 100vw;
    display: flex;
    align-items: center;
    padding: 0 15px;
    background: lightgray;
}

.navbar__links{
    margin-left: auto;
    display: flex;
    color: teal;
    padding: 15px;
}

.link{
    padding: 5px 15px;
    color: teal;
    font-size: 14 px;
    background: transparent;
    border: 1px solid teal;
    cursor: pointer;
    text-decoration: none;
}
.MyInput_myInput__19BxJ{
