}
.MyModal_myModal__1CKEf {
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    display: none;
    background: rgba(0, 0, 0, 0.5);
}

.MyModal_myModalContent__2Zepo {
    padding: 25px;
    background: white;
    border-radius: 16px;
    min-height: 250px;
}

.MyModal_myModal__1CKEf.MyModal_active__WtdSS {
    display: flex;
    justify-content: center;
    align-items: center;
}
.Loader_loader__WMZrA{
    width: 100px;
    height: 100px;
    border-radius: 50%;
    border: 3px dashed teal;
    -webkit-animation: Loader_rotate__2skYJ 1s infinite linear;
            animation: Loader_rotate__2skYJ 1s infinite linear;
}

@-webkit-keyframes Loader_rotate__2skYJ{
    from{
        -webkit-transform: rotate(0deg) scale(1);
                transform: rotate(0deg) scale(1);
    }
    to{
        -webkit-transform: rotate(360deg) scale(1.2);
                transform: rotate(360deg) scale(1.2);
    }
}

@keyframes Loader_rotate__2skYJ{
    from{
        -webkit-transform: rotate(0deg) scale(1);
                transform: rotate(0deg) scale(1);
    }
    to{
        -webkit-transform: rotate(360deg) scale(1.2);
                transform: rotate(360deg) scale(1.2);
    }
}
